@import url(https://fonts.googleapis.com/css?family=Prata&display=swap);
*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;

  max-width: 100%;
}

a{
  text-decoration: none;
  color: #313131;
}

body{
  font: 400 22px Prata, sans-serif;
  background-image: url(/static/media/hairstylist.956917d0.png);
  background-size: cover;
  -webkit-font-smoothing: antialiased;
}

@media(max-width: 960px){
  *{
    max-width: 100%;
  }
  .container-home img{
    width: 90%;
    height: auto;
    align-self: flex-end;
  }
}
.container-home{
    position: relative;

    display: flex;
    flex-direction: row;

    justify-content: flex-end;

    width: 1720px; 
    height: calc( 100vh - 100px );
    margin: auto;
}
.container-home .title{
    position: absolute; 

    left: 0px;
    top: 80px;
    font-weight: 400;
}


.container-home .title-session{
    display: flex;
    justify-content: center;

    margin-top: 120px;

    width: 100%; 
    text-align: center;
}

@media(max-width: 960px){
    *{
        max-width: 100%;
    }
    .container-home{
        max-width: 90%;
        margin: auto;
    }
}
header{
    height: 100px;
    width: 100%;
}

header .container{
    display: flex;
    
    width: 1720px;
    height: 100px;
    
    margin: auto;

    justify-content: space-between;
    align-items: center;
}

header .container .logo{ 
    height: 70px;
    max-height: 100%;
}

header .container .menu {
    display: flex;
    flex-direction: row;

    list-style: none;
} 

header .container .menu a{
    text-decoration: none;
    color: #313131
}

header .container .menu a li { 
    width: 120px;
    text-align: center;
}

.menu-mobile-button{
    position: absolute;

    display: none;

    top: 30px; 
    right: 50px;

    z-index: 999;
}

.menu-mobile{ 
    position: absolute;

    top: -800px;

    display: flex;
    width: 100%;

    background-color: rgba(236, 240, 241, 0.9);
    box-shadow: 0px 13px 300px 0px rgba(0,0,0,0.4);

    -webkit-transition: top 0.8s;

    transition: top 0.8s;

    z-index: 99;
}

.menu-mobile ul{
    width: 100%;
    list-style: none;
}

.menu-mobile ul a li{
    width: 100%;
    padding: 20px 0px;

    text-align: center;

    border-bottom: 1px solid #313131;
}

@media(max-width: 860px){
    header .container{
        width: 90%;
        margin: auto
    }
    header .container .menu{
        display: none;
    }
    .menu-mobile-button{
        display: flex;
    }
}
.container-services{
    position: relative;

    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;

    width: 1720px; 
    height: auto;
    margin: auto;
}

.container-services .title{
    padding: 20px 0px 80px 0px;
}

.container-services .services{
    display: flex;
    flex-direction: row;

    justify-content: space-between;
}

.container-services .services .service{
    display: flex;
    flex-direction: column;

    margin: auto 70px;

    justify-content: center;
    align-items: center;
}

.container-services .services .service img{
    width: 250px
}

.container-services .services div h4{
    text-align: center;
    font-weight: normal;
    color: #808080;

    margin-top: 30px;
}

.container-services .final-text h3{
    margin-top: 30px;

    font-weight: normal;
    text-align: center;
}

@media(max-width: 860px){
    .container-services .services{
        flex-direction: column;
    }

    .container-services .services .service{
        padding: 20px 0px;
    }

    .container-services .services .service img{
        height: 200px;
        width: auto;
    }
    .container-services .final-text h3{
        font-size: 24px;
    }
}
.container-contact{
    position: relative;

    display: flex;
    flex-direction: column;

    align-items: center;

    width: 1080px; 
    height: calc( 100vh - 100px );
    margin: auto;
}

.container-contact .title{
    padding: 20px 0px 80px 0px;
}
.container-contact .textContact{ 
    color: #313131;
    font-weight: normal;
}
.container-contact .textContact h3{
    text-align: center;
}
.container-contact .icons{
    margin-top: 40px;
}

.addressText{
    position: absolute; 

    bottom: 50px;
    padding: 0px 20px;
    margin-top: 120px;
    
    text-align: center;
    font-weight: lighter;
    font-style: italic;
}
